import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepContent from '@material-ui/core/StepContent';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { connect } from 'react-redux';
import axios from 'axios';

// Components
import GeneralInfo from './Forms/GeneralInfo';
import VitalSigns from './Forms/Vitals';

const styles = theme => ({
  root: {
    width: '90%',
    overflowY: 'scroll',
    height: '85vh'
  },
  button: {
    marginTop: theme.spacing.unit,
    marginRight: theme.spacing.unit,
  },
  actionsContainer: {
    marginBottom: theme.spacing.unit * 2,
  },
  resetContainer: {
    padding: theme.spacing.unit * 3,
  },
});

function getSteps() {
  return ['Patient Info', 'Vitals', 'Other Info'];
}

function getStepContent(step) {
  switch (step) {
    case 0:
      return (
        <GeneralInfo />
      )
    case 1:
      return(
        <VitalSigns />
      )
    case 2:
      return `Try out different ad text to see what brings in the most customers,
              and learn how to enhance your ads using features like ad extensions.
              If you run into any problems with your ads, find out how to tell if
              they're running and how to resolve approval issues.`;
    default:
      return 'Unknown step';
  }
}

class VerticalLinearStepper extends React.Component {
  state = {
    activeStep: 0,
  };

  handleNext = () => {
    this.setState(state => ({
      activeStep: state.activeStep + 1,
    }));
  };

  handleBack = () => {
    this.setState(state => ({
      activeStep: state.activeStep - 1,
    }));
  };

  handleReset = () => {
    this.setState({
      activeStep: 0,
    });
  };

  handleSubmit = () => {
    axios.post('https://smart-care-api-shadid12.c9users.io/patients', {
      "firstname": this.props.firstname,
      "lastname": this.props.lastname,
      "age": this.props.age,
      "gender": this.props.gender,
      "allergies": this.props.allergies,
      "primary_diagonosis": this.props.primary_diagonosis,
      "physician": this.props.physician
    }).then((res) => {
      console.log(res);
    })
  };

  render() {
    const { classes } = this.props;
    const steps = getSteps();
    const { activeStep } = this.state;

    return (
      <div className={classes.root}>
        <Stepper activeStep={activeStep} orientation="vertical">
          {steps.map((label, index) => {
            return (
              <Step key={label}>
                <StepLabel>{label}</StepLabel>
                <StepContent>
                  <Typography>{getStepContent(index)}</Typography>
                  <div className={classes.actionsContainer}>
                    <div>
                      <Button
                        disabled={activeStep === 0}
                        onClick={this.handleBack}
                        className={classes.button}
                      >
                        Back
                      </Button>
                      <Button
                        variant="contained"
                        color="primary"
                        onClick={this.handleNext}
                        className={classes.button}
                      >
                        {activeStep === steps.length - 1 ? 'Finish' : 'Next'}
                      </Button>
                    </div>
                  </div>
                </StepContent>
              </Step>
            );
          })}
        </Stepper>
        {activeStep === steps.length && (
          <Paper square elevation={0} className={classes.resetContainer}>
            <Typography>All steps completed - you&quot;re finished</Typography>
            <Button onClick={this.handleReset} className={classes.button}>
              Reset
            </Button>
            <Button onClick={this.handleSubmit} 
                    variant="contained" 
                    color="secondary"
                    className={classes.button}
            >
              Submit
            </Button>
          </Paper>
        )}
      </div>
    );
  }
}

VerticalLinearStepper.propTypes = {
  classes: PropTypes.object,
};

function mapStateToProps (state) {
  const {firstName, lastName, age, gender, allergies, physician, primaryDiagonosis } = state
  return {firstName, lastName, age, gender,  allergies, physician, primaryDiagonosis}
}

export default connect(mapStateToProps)(withStyles(styles)(VerticalLinearStepper));